.content-block {
  /*height: 100vh;*/
  height: 100%;
  display: flex;
  align-self: center;
  justify-content: center;
  flex-direction: column;
  /*padding-bottom: 100px;*/
}
.content-block__elem {
  /*height: 75vh;*/
