.header {
  /*min-height: 1000px;*/
  height: 100vh;
  /*border: 1px solid red;*/
  background: url('../img/gif_header.gif');
  display: flex;
  align-items: flex-end;
}
.header__inner {
  display: flex;
  width: 100%;
  /*flex-direction: column;*/
  /*align-items: center;*/
  /*padding-bottom: 50px;*/
}
.header__preview-container {
  width: 90%;
  background-color: #7d7d7d;
  /*background: linear-gradient(45deg, rgba(126,24,223,1) 0%, rgba(190,170,233,1) 100%);*/
