  border-top: 2px solid black;
  border-right: 2px solid black;
  border-bottom: 2px solid black;
  /*background-image: linear-gradient(to top right, rgb(203, 203, 207), rgb(198, 196, 201), rgb(192, 190, 195), rgb(187, 183, 188), rgb(182, 176, 182), rgb(177, 170, 176), rgb(171, 163, 170), rgb(166, 157, 164), rgb(161, 150, 158), rgb(156, 143, 151), rgb(150, 137, 145), rgb(145, 130, 139));*/
