.code__keyword {
  color: #00458b;
}
.code__arrow {
  color: steelblue;
}
.code__eval,
.code__int,
.code__value,
.code__operator {
  color: orange;
}
.code__string {
  color: green;
}
.code__operator,
.code__point,
.code__boolean {
  color: crimson;
}
.code__console-value {
  color: blue;
}
.code__console-string {
  color: crimson;
}
.code__statement-operator {
  color: orchid;
}
@media (max-width: 940px) {
  .code {
    font-size: 12px;
    overflow: scroll;
  }
  .code--width-36,
  .code--width-54,
  .code--width-57,
  .code--width-67 {
    width: 95%;
  }
}
